import {SellerOrderInprogressComponent} from './seller-all-order/seller-order-inprogress/seller-order-inprogress.component';
import {SellerOrderComponent} from './seller-order.component';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {OrderTabAllComponent} from './seller-all-order/order-tab-all/order-tab-all.component';

const routes: Routes = [
	{
		path: '',
		component: SellerOrderComponent,
		children: [
			{path: 'orders', component: OrderTabAllComponent, data: {title: 'Seller-app.content.body.order.order'}},
			{
				path: 'inprogress',
				component: SellerOrderInprogressComponent,
				data: {title: 'Seller-app.content.body.order.inprogress'}
			}
		]
	}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class SellerOrderRoutingModule {}
